import React, {useEffect, useState} from 'react';
import Container from "react-bootstrap/Container";
import {Button, Form, Modal, Row} from "react-bootstrap";
import Col from "react-bootstrap/Col";
import './scss/Aside.scss'
import TestBar from "../components/aside/TestBar";
import wrapperTool from "../components/hoc/wrapperTool";
import {useSelector} from "react-redux";
import {Redirect} from "react-router-dom";
import swal from 'sweetalert';
import {AxiosBe} from "../utils/axios";
import Editor from "../editor/Editor";
import {toTime} from "../helpers/helpers";
import qs from 'querystring';
import Loading from "../components/common/Loading";
import PageLoading from "../components/common/PageLoading";

const Test = () => {
    const course = useSelector(state => state.course.course);
    const user = useSelector(state => state.main.user);
    const [assessment, setAssessment] = useState(null);
    const [loading, setLoading] = useState(false);
    const [test, setTest] = useState(null);
    const [expire, setExpire] = useState(false);
    const [assessmentAnswer, setAssessmentAnswer] = useState(null);
    const [assessmentInfo, setAssessmentInfo] = useState(null);
    const [assessmentResult, setAssessmentResult] = useState(null);
    const [showResult, setShowResult] = useState(false);

    const handleChangeCode = (item, code) => {
        if (assessmentAnswer) {
            const index = assessmentAnswer.findIndex(quiz => item.id === quiz.id);
            assessmentAnswer[index].code = code
        }
    };
    const handleChangeAnswer = (item, answer) => {

        if (assessmentAnswer) {
            const index = assessmentAnswer.findIndex(quiz => item.id === quiz.id);
            assessmentAnswer[index].answer = answer;
        }
    };
    const handleStart = () => {
        setExpire(false);
        if (parseInt(assessmentInfo.attemptNumber) >= 3) {
            swal({
                title: "Bn  vt qu s ln lm bi (ti a 3 ln)",
                icon: "warning",
                buttons: false,
                timer: 1500,
                dangerMode: true,
            }).then()
        } else
            swal({
                title: "Bt u lm bi ?",
                icon: "warning",
                buttons: true,
                dangerMode: true,
            }).then(r => {
                if (r)
                    setLoading(true);
                AxiosBe.get(`/api/assessment?courseId=${course.id}&userId=${user.id}`)
                    .then(({data: res}) => {
                        if (res.success) {
                            setAssessment(res.data)
                            setTest({...res.assessment, total: res.assessment.duration});
                            setAssessmentAnswer(res.data.map((item, index) => {
                                return {
                                    index: index,
                                    id: item.id,
                                    kindChallengeId: item.kindChallengeId,
                                    code: item.code,
                                    answer: ''
                                }
                            }))
                            window.scrollTo(0, 0);
                        } else {
                            swal({
                                title: "Hin ti cha c  thi no !",
                                icon: "error",
                                buttons: false,
                                timer: 1500
                            }).then(r => r)
                        }
                    })
                    .catch(err => {
                        console.log(err)
                    })
                    .finally(_ => {
                        setLoading(false);
                    })
            })
    }
    const submit = (payload) => {
        AxiosBe.post('/api/submitAssessment', qs.stringify(payload))
            .then(({data: res}) => {
                if (res.success) {
                    setShowResult(true)
                    setAssessmentInfo(res.AS);
                    setAssessmentResult({results: res.data, time: res.time})
                    reset()
                }
            })
            .catch(err => {
                swal({
                    title: "C li xy ra ,vui lng th li",
                    icon: 'error',
                    timer: 1500,
                    button: false
                }).then()
            })
            .finally(_ => {
                setLoading(false);
            })
    }
    const handleSubmit = () => {
        if (test)
            swal({
                title: "Np bi ?",
                icon: "info",
                buttons: true,
                dangerMode: true,
            }).then(r => {
                if (r) {
                    setExpire(true);
                    setLoading(true);
                    let payload = {
                        userId: user.id,
                        assessmentId: test.id,
                        courseId: course.id,
                        time: test.total - test.duration,
                        data: JSON.stringify(assessmentAnswer)
                    }
                    submit(payload);
                }
            })
        else {
            swal({
                title: " ht gi lm bi",
                icon: "info",
                buttons: false,
                timer: 1500
            }).then(r => r)
            setExpire(true);
            setLoading(true);
            let payload = {
                userId: user.id,
                assessmentId: test.id,
                courseId: course.id,
                time: test.total - test.duration,
                data: JSON.stringify(assessmentAnswer)
            };
            submit(payload)

        }
    }

    function reset() {
        setAssessment(null)
    }

    function hideResult() {
        setShowResult(false);
        setAssessmentResult(null);
    }

    useEffect(_ => {
        if (course && user)
            AxiosBe.get(`/api/assessment/${course.id}/${user.id}`)
                .then(({data: res}) => {
                    if (res.success)
                        setAssessmentInfo(res.AS);
                    else {
                        setAssessmentInfo({error: true})
                    }
                })
                .catch(err => {
                    setAssessmentInfo({error: true})
                })
    }, [course, user]);

    useEffect(_ => {
        if (test) {
            if (!expire)
                if (test.duration !== 0)
                    setTimeout(_ => {
                        setTest({...test, duration: test.duration - 1})
                    }, 1000)
                else {
                    handleSubmit()
                }
            else
                setTest(null)
        }
        // eslint-disable-next-line
    }, [test, expire])

    if (course)
        return (
            <Container fluid={true} className={'Content'}>
                {loading && <PageLoading/>}
                <Row>
                    <Col className={'Aside__Tool'} xs={3}>
                        {assessmentInfo ?
                            !assessmentInfo.error ?
                                <TestBar info={assessmentInfo} startTest={handleStart} assessment={assessment}/> :
                                <h5 className={'mt-5 text-danger'}>Bi kim tra ca kho hc cha c to !</h5> :
                            <Loading/>}
                    </Col>
                    <Col xs={3}/>
                    {
                        !assessment ?
                            <Col xs={9} className={'p-4'}>
                                <h4 className={'Title text-left my-4'}>Bi Kim Tra</h4>
                                <p>Bi kim tra l bc cui cng trc khi bn kt thc qu trnh rn luyn trn nn
                                    tng ny.
                                    Bi kim tra bao gm mt s cu hi c thit k  nh gi kt qu hc tp ca
                                    bn..</p>
                                <p>ng qu lo lng, bi v im s  y khng c s dng chnh thc  nh gi qu
                                    trnh
                                    hc ca bn, m n ch c s dng  phn nh trnh  ca bn sau khi  hon
                                    thnh cc
                                    bi hc v phn rn luyn trn nn tng ny. Ngoi ra, bn khng b hn ch s ln
                                    lm bi
                                    kim tra. Hy lm li nu bn t im s qu thp..</p>
                                <p>Hy bt u no!!</p>
                            </Col>
                            :
                            <Col xs={6} className={'p-4'}>
                                {assessment.length ?
                                    <div>
                                        <section>
                                            {assessment.map((item, index) => {
                                                if (item.kindChallengeId === 1)
                                                    return <div className={'mb-5'} key={item.id}>
                                                        <h6 className={'title mb-2'}>Cu {index + 1}</h6>
                                                        <h5>{item.title} (in p n)</h5>
                                                        <h6>{item.question}</h6>
                                                        <Editor readOnly={true} code={item.code}
                                                                type={course['LanguageChallenges'][0]['title']}
                                                                change={(code) => {
                                                                }}/>
                                                        <Form.Control
                                                            onChange={(e) => handleChangeAnswer(item, e.target.value)}
                                                            className={"mt-2"} type="text"
                                                            placeholder="Nhp p n ..."/>
                                                    </div>
                                                else
                                                    return <div className={'mb-5'} key={item.id}>
                                                        <h6 className={'title mb-2'}>Cu {index + 1}</h6>
                                                        <h5>{item.title} (vit code)</h5>
                                                        <h6>{item.question}</h6>
                                                        <Editor code={item.code}
                                                                type={course['LanguageChallenges'][0]['title']}
                                                                change={(code) => {
                                                                    handleChangeCode(item, code)
                                                                }}/>
                                                    </div>
                                            })}
                                            <hr/>
                                        </section>
                                        <Button className={"mt-5"} onClick={handleSubmit} block={true}>Np bi</Button>
                                    </div>
                                    :
                                    <div className={'mb-5'}>
                                        <h4 className={'text-muted'}>Hin cha c cu hi no !</h4>
                                    </div>
                                }
                            </Col>

                    }
                    <Modal show={showResult} onHide={() => hideResult()} dialogClassName="modal-400w">
                        <Modal.Header closeButton>
                            <Modal.Title>
                                <h1 className={'title mb-0'}>Kt qu</h1>
                            </Modal.Title>
                        </Modal.Header>
                        <Modal.Body>
                            {assessmentResult && <div>
                                <h6 className={'text-center mb-4'}>Thi gian lm bi : ({assessmentResult.time})</h6>
                                <div className={"d-flex flex-column"} style={{width: '300px', margin: '0 auto'}}>
                                    {
                                        assessmentResult.results.sort((a, b) => a.index - b.index).map(result => {
                                            if (result.answer)
                                                return <div key={result.index}
                                                            className="btn btn-success mb-2">Cu {result.index + 1}</div>
                                            else
                                                return <div key={result.index}
                                                            className="btn btn-danger mb-2">Cu {result.index + 1}</div>
                                        })
                                    }
                                </div>
                                <div className={"mt-5"}>
                                    <div onClick={() => hideResult()} className="btn btn-info mb-2 w-100">Xc
                                        Nhn
                                    </div>
                                </div>
                            </div>}
                        </Modal.Body>
                    </Modal>
                </Row>
                {
                    test ?
                        <div className={'test__bottom'}>
                            <Row>
                                <Col>
                                    <div className={'d-flex justify-content-center align-items-center'}
                                         style={{height: 50}}>
                                        <span className={"mr-3"}>Thi gian cn li : </span>
                                        <div className={'test__bar'}>
                                        <span className={'test__bar__process'}
                                              style={{width: (test.duration / test.total) * 100 + '%'}}/>
                                        </div>
                                        <span className={"ml-3"}>{toTime(test.duration)}</span>
                                    </div>
                                </Col>
                            </Row>
                        </div>
                        :
                        null
                }
            </Container>
        );
    else
        return <Redirect to={'/'}/>
};

export default wrapperTool(Test);