import React, {useState} from 'react';
import {Modal} from "react-bootstrap";
import useForm from "../../helpers/userForm";
import {changePass} from "../../actions/rootActions";
import {useDispatch, useSelector} from "react-redux";
import {useHistory} from "react-router-dom";
import swal from "sweetalert";

const ModalChangePass = (props) => {
    const user = useSelector(state => state.main.user);
    let [resErr, setResErr] = useState('');
    const stateSchema = {
        pass: '',
        newPass: '',
        newPassRe: ''
    };
    const dispatch = useDispatch();
    const history = useHistory();
    const validate = ()=>{
        let errors = {};
        //validate name
        if (!values.pass)
            errors.pass = 'Bn cha nhp mt khu c !';

        if (!values.newPass){
            errors.newPass = 'Mt khu mi khng c  trng !';
        }else if (values.newPass.length < 6)
            errors.password = 'Password khng t hn 6 k t !';

        if (values.newPassRe !== values.newPass){
            errors.newPassRe = 'Khng trng vi mt khu mi!';
        }else if (values.newPassRe.length < 6)
            errors.password = 'Password khng t hn 6 k t !';

        return errors;
    }
    const {handleChange, handleSubmit, values, errors} = useForm(stateSchema, submit, validate);
    const handleClose = () => {
        props.closeModal()
    };

    function submit() {
        dispatch(changePass({email:user.email,password:values.pass,newPassword:values.newPass}))
            .then(({data: res}) => {
                if (res.success) {
                    swal({
                        title: "i mt khu thnh cng. Vui lng ng nhp li",
                        icon: "success",
                        button: false,
                        timer:1500
                    }).then(r => r)
                    localStorage.removeItem('token');
                    history.push('/?login')
                    // resetForm();
                } else {
                    setResErr(res.message)
                }
            })
            .catch(err => {
                setResErr(err.message)
            })
    }

    return (
        <Modal show={props.show} onHide={handleClose} id={'ModalRegister'}>
            <Modal.Body className={'p-5'}>
                <h4 className={'Title text-center pb-4'}>i mt khu</h4>
                {resErr && <div className="alert alert-danger" role="alert">
                    {resErr}
                </div>}
                <form noValidate onSubmit={handleSubmit}>
                    <div className="form-group">
                        <label htmlFor="">Mt khu c</label>
                        <input
                            name={'pass'} type="password" placeholder={'Mt khu c *'} value={values.pass}
                            onChange={handleChange}
                        />
                        {errors.pass && <small className={'text-danger'}>{errors.pass}</small>}
                        {/*error message here*/}
                    </div>
                    <div className="form-group">
                        <label htmlFor="">Mt khu mi</label>
                        <input
                            onChange={handleChange}
                            name={'newPass'} type="password" placeholder={'Mt khu mi *'} value={values.newPass}
                        />
                        {errors.newPass && <small className={'text-danger'}>{errors.newPass}</small>}
                        {/*error message here*/}
                    </div>
                    <div className="form-group">
                        <label htmlFor="">Xc nhn mt khu</label>
                        <input
                            onChange={handleChange}
                            name={'newPassRe'} type="password" placeholder={'Nhp li mt khu mi *'} value={values.newPassRe}
                        />
                        {errors.newPassRe && <small className={'text-danger'}>{errors.newPassRe}</small>}
                        {/*error message here*/}
                    </div>
                    <div className="form-group pt-4">
                        <button type={'submit'} className={'Button Button--full'}>Gi</button>
                    </div>
                </form>
            </Modal.Body>
        </Modal>
    );
};

export default ModalChangePass;