import React, {useEffect, useState} from 'react';
import './scss/Home.scss';
import {Col, Container, Row} from "react-bootstrap";
import ArticleIntro from "../components/home/ArticleIntro";
import Banner from '../assets/img/Banner.jpg';
import imgBaiHoc from '../assets/img/home/book.png';
import imgLuyenTap from '../assets/img/home/practice.png';
import imgKiemTra from '../assets/img/home/coding.png';
import imgMienPhi from '../assets/img/home/computer.png';
import {AxiosBe} from "../utils/axios";
import CountUp from "react-countup";
import {useSelector} from "react-redux";
import {useHistory} from "react-router-dom";

const Home = (props) => {
    const user = useSelector(state => state.main.user);
    const history = useHistory();
    const [counts, setCounts] = useState([]);
    useEffect(_ => {
        if (!counts.length)
            AxiosBe.get('/api/home')
                .then(({data: res}) => {
                    setCounts(res.data);
                }).catch(err => {
                console.log(err)
            })
    }, [counts.length]);
    const list = [
        {
            img: imgBaiHoc,
            title: 'Bi hc',
            content: 'Chui cc bi hc c chia lm 11 mc  ni tip nhau, gip cho ngi hc i t nhng khi nim c bn nht cho n nhng bi ton gii quyt vn .'
        },
        {
            img: imgLuyenTap,
            title: 'Luyn tp',
            content: 'Chui cc bi luyn tp gip hc vin thc hnh k nng gii quyt vn  v p dng cc thut ton thng dng trong nhng bi ton thng gp.'
        },
        {
            img: imgKiemTra,
            title: 'Bi kim tra',
            content: 'Bi kim tra ngn l bc cui cng  hc vin hon thnh cc ni dung luyn tp, gip hc vin bit c tnh trng ca mnh sau mt thi gian luyn tp.'
        },
        {
            img: imgMienPhi,
            title: 'Min ph',
            content: 'Nn tng Small Code c cung cp hon ton min ph nhm to iu kin tt nht cho tt c mi ngi c c hi tip cn v luyn tp bt c lc no.'
        },

    ];

    const handleClick  = ()=>{
        if (user){
            history.push('/courses')
        }else{
            console.log("aaa")
            props.onClick()
        }
    }

    return (
        <div className={'Home'}>
            <div className={'Home__Banner'}>
                <div className={'Home__Banner-content'}>
                    <Container>
                        <Row>
                            <Col xs={6}>
                                <h1>Cho mng n vi Small Code!</h1>
                                <h5>
                                    Small Code l mt nn tng h tr cc lp trnh vin hc, luyn tp thut ton v k
                                    nng gii quyt vn . Cc bi luyn tp trong Small Code c thit k  dn dt
                                    v nng k nng ca lp trnh vin theo tng cp .

                                    Nn tng Small Code c cung cp hon ton min ph.
                                </h5>
                            </Col>
                        </Row>
                    </Container>
                </div>
                <img className={'Home__Banner-img'} src={Banner} alt=""/>
            </div>
            <div>
                <Container>
                    <Row>
                        {
                            counts.length ? counts.map((count, index) => <Col className={"my-5"} key={index}>
                                <div className={'count-up'} onClick={handleClick}>
                                    <h2><CountUp delay={0.5} end={count.count} duration={5}/></h2>
                                    <h5>{count.title}</h5>
                                </div>
                            </Col>)
                                : null
                        }
                    </Row>
                </Container>
            </div>
            <div className={'Home__Info my-5'}>
                <Container>
                    <Row>
                        <div className={'card-deck'}>
                            {
                                list.slice(0, 2).map((article, index) => {
                                    return <Col key={index}><ArticleIntro onClick={handleClick} article={article} /></Col>
                                })
                            }
                        </div>
                    </Row>
                    <Row>
                        <div className={'card-deck'}>
                            {
                                list.slice(2, 4).map((article, index) => {
                                    return <Col key={index}><ArticleIntro onClick={handleClick} article={article} /></Col>
                                })
                            }
                        </div>
                    </Row>
                </Container>
            </div>
        </div>
    )
}

export default Home