export default function validate(values) {
    let errors = {};
    //validate email
    if (!values.email)
        errors.email = 'Email khng c  trng !';
    else if (!/^.+@.+\..+$/.test(values.email))
        errors.email = 'Email khng hp l !';
    //validate password
    if (!values.password)
        errors.password = 'Password khng c  trng !';
    else if (values.password.length < 6)
        errors.password = 'Password khng t hn 6 k t !';
    return errors;
}